(function( $ ) {
	'use strict';
	
	/**
	 * Initialize the date picker for the admin.
	 */
	function initDatePicker() {
		$( '.em-cookie-notification-datepicker' ).datepicker( {
			dateFormat : 'mm/dd/yy'
		} ); 
	}
	
	/**
	 * Waits until the document is loaded before kicking things off.
	 */
	$( document ).ready(function() {
		initDatePicker();
	});
	
})( jQuery );
